.form .wpcf7-submit {
  display: inline-block;
  background-color: #9dcd48;
  border: 0.125rem solid #9dcd48;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 400;
  text-decoration: none;
  color: #ffffff;
  padding: 0.9375rem;
  transition: all 0.3s ease;
  width: 12.5rem;
  text-align: center;
}
.form .wpcf7-submit:hover {
  background: #ffffff;
  color: #9dcd48;
}
.form .wpcf7-spinner {
  display: none;
}
.form input[type=checkbox] {
  opacity: 0;
  position: absolute;
  left: -624.9375rem;
}
.form span.wpcf7-list-item span::before {
  content: "";
  display: inline-block;
  height: 1rem;
  width: 1rem;
  border: 0.0625rem solid #d4d4d4;
  flex-shrink: 0;
}
.form span.wpcf7-list-item span::after {
  position: absolute;
  content: "";
  display: inline-block;
  height: 0.375rem;
  width: 0.5625rem;
  border-left: 0.125rem solid;
  border-bottom: 0.125rem solid;
  transform: rotate(-45deg);
}
.form span.wpcf7-list-item span {
  position: relative;
  display: flex;
}
.form span.wpcf7-list-item span::before {
  margin-top: 0.1875rem;
  margin-right: 0.625rem;
  flex-shrink: 0;
}
.form span.wpcf7-list-item span::after {
  left: 0.25rem;
  top: 0.3125rem;
  content: none;
}
.form span.wpcf7-list-item input[type=checkbox]:checked + span::after {
  content: "";
}
.form span.wpcf7-list-item input[type=checkbox]:focus + span::before {
  outline: 0;
}

